.card {
  background: #fff;
  border-radius: 0;
  height: 325px;
  margin: 1rem;
  position: relative;
  z-index: 7;
  width: 325px;
  box-shadow: 0 0px 37px #27ff00, 0 0px 11px #0077ff;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  /*overflow: hidden;*/
  text-align: center;
  background:#202223;
}
.card > .img-container > span > img {
  width: 100%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.style_prevu_kit
{
    display:inline-block;
    border:0;
    width:200px;
    height:220px;
    position: relative;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1); 
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1); 
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1);
    transition: all 200ms ease-in;
    transform: scale(1);   
}

.style_prevu_kit:hover
{
    cursor:pointer;
    box-shadow: 0px 0px 150px #000000;
    z-index: 2;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1.3);
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1.3);   
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1.3);
    transition: all 200ms ease-in;
    transform: scale(1.3);
}

